@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
	margin: 0;
	font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI',
		'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: rgb(5, 136, 212);
	background: linear-gradient(
		90deg,
		rgba(5, 136, 212, 1) 0%,
		rgba(245, 98, 23, 1) 100%
	);
	font-family: 'Courier New', Courier, monospace;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

nav {
	display: flex;
	justify-content: flex-end;
}

.p-nav {
	padding: 1rem;
	font-size: 1.25rem;
	text-decoration: underline;
	transition: all 0.15s ease-in;
	color: rgb(0, 0, 0);
}

.p-nav:hover {
	cursor: pointer;
	opacity: 0.7;
}

.logo {
	margin: 2rem;
	margin-top: 0;
	display: flex;
	justify-content: center;
}

.Tilt {
	border-radius: 0.25rem;
	box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.2);
	background: rgb(5, 136, 212);
	background: linear-gradient(
		90deg,
		rgba(5, 136, 212, 1) 0%,
		rgba(245, 98, 23, 1) 100%
	);
	transition: -webkit-transform 0.2s;
	transition: transform 0.2s;
	transition: transform 0.2s, -webkit-transform 0.2s;
}

.Tilt:hover {
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
}

.Tilt-inner {
	padding: 1rem;
}

.rank {
	font-size: 1.5rem;
	color: #ffffff;
}

.bold {
	font-size: 1.75rem;
	font-weight: 700;
}

.p-img-link-form {
	font-size: 1.5rem;
}

.center {
	display: flex;
	justify-content: center;
}

.form1 {
	width: 50vw;
	display: flex;
	justify-content: center;
	padding: 2rem;
	border-radius: 0.25rem;
	box-shadow: 4px 4px 8px 0 rgb(0 0 0 / 20%);
	background: linear-gradient(
			45deg,
			#dca 12%,
			transparent 0,
			transparent 88%,
			#dca 0
		),
		linear-gradient(135deg, transparent 37%, #a85 0, #a85 63%, transparent 0),
		linear-gradient(45deg, transparent 37%, #dca 0, #dca 63%, transparent 0)
			#753;
	background-size: 25px 25px;
}

@media only screen and (max-width: 800px) {
	.form {
		width: 70vw;
	}
}

@media only screen and (max-width: 400px) {
	.form {
		width: 90vw;
	}
}

.input-img-link-form {
	padding: 0.5rem;
	width: 70%;
	margin-right: auto;
	margin-left: auto;
}

.button-img-link-form {
	font-size: 1.25rem;
	width: 30%;
	transition: all 0.2s ease-in-out;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	padding-left: 1rem;
	padding-right: 1rem;
	display: inline-block;
	color: #ffffff;
	background-color: #9d8c83;
	cursor: pointer;
}

.button-img-link-form:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.center {
	display: flex;
	justify-content: center;
}

.boudning-box {
	position: absolute;
	box-shadow: 0 0 0 3px #f7ff02 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}

.box-container {
	width: 305px;
	min-height: 550px;
	display: flex;
	flex-direction: column;
	border-radius: 19px;
	background-color: #fff;
	box-shadow: 0 0 2px rgba(15, 15, 15, 0.28);
	position: relative;
	overflow: hidden;
}

.top-container {
	width: 100%;
	height: 150px;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	padding: 0 1.8em;
	padding-bottom: 5em;
}

.inner-container {
	padding: 10px;
	/* width: 100%; */
	display: flex;
	flex-direction: column;
	padding: 0 1.8em;
}

.back-drop {
	width: 160%;
	height: 500px;
	position: absolute;
	display: flex;
	flex-direction: column;
	border-radius: 80%;
	/* transform: rotate(50deg); */
	top: -300px;
	left: -140px;
	background: rgb(88, 88, 94);
	background: rgb(245, 98, 23);
	background: linear-gradient(
		360deg,
		rgba(245, 98, 23, 1) 0%,
		rgba(5, 136, 212, 1) 50%
	);
	transition: height 1s;
}

.back-drop-expanded {
	height: 1245px;
}

.header-container {
	width: 100%;
	display: flex;
	flex-direction: column;
}

.header-text {
	font-size: 30px;
	font-weight: 600;
	line-height: 1.24;
	color: #fff;
	z-index: 10;
	margin: 0;
}

.small-text {
	font-size: 11px;
	font-weight: 500;
	color: #fff;
	z-index: 10;
	margin: 0;
	margin-top: 8px;
}

.box-login-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 10px;
}

.form-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	box-shadow: 0px 0px 2.5px rgba(15, 15, 15, 0.19);
}

.muted-link {
	font-size: 12px;
	color: rgba(200, 200, 200, 0.8);
	font-weight: 500;
	text-decoration: none;
}

.bold-link {
	font-size: 12px;
	color: rgb(5, 136, 212);
	font-weight: 500;
	text-decoration: none;
}

.input {
	height: 42px;
	outline: none;
	border: 1px solid rgba(200, 200, 200, 0.1);
	padding: 0px 10px;
	border-bottom: 1.4px solid transparent;
	transition: all 200ms ease-in-out;
	font-size: 12px;
}

.input::-webkit-input-placeholder {
	outline: none;
}

.input:-ms-input-placeholder {
	outline: none;
}

.input::placeholder {
	outline: none;
}

.input:not(:last-of-type) {
	border-bottom: 1.5px solid rgba(200, 200, 200, 0.4);
}

.input:focus {
	outline: none;
	border-bottom: 2px solid rgb(5, 136, 212);
}

.submit-button {
	width: 100%;
	padding: 11px 40%;
	color: #fff;
	font-size: 15px;
	font-weight: 600;
	border: none;
	border-radius: 100px 100px 100px 100px;
	cursor: pointer;
	transition: all, 240ms ease-in-out;
	background: rgb(5, 136, 212);
	background: linear-gradient(
		90deg,
		rgba(5, 136, 212, 1) 0%,
		rgba(245, 98, 23, 1) 100%
	);
}

.submit-bottom:hover {
	-webkit-filter: brightness(1.03);
	        filter: brightness(1.03);
}

.app-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: left;
}

